<template>
	<div id="kabao-record">
		<public-header title="积分记录"></public-header>
		<div class="content">
			<div class="tab-group">
				<div class="tab-item"
					:class="{active: filter.items.trade_type == index}"
					@click="onToggleTradeType(index)"
					v-for="(item, index) in trade_types"
					:key="index"
				>
					{{item}}
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
				>
					<div class="current-list">
						<div class="list-block">
							<ul class="list">
								<li class="item-content" v-for="(item, recordIndex) in records.data" :key="recordIndex">
									<div class="item-inner" :class="{'van-hairline--top': recordIndex > 0}">
										<div class="item-title">
											<div class="item-title-top">{{item.trade_type_cn}}</div>
											<div class="item-title-bottom">{{item.addtime_cn}}</div>
										</div>
										<div class="item-after">
											<div class="item-title-top" :class="{'color-success': item.fee > 0, 'color-danger': item.fee < 0}">{{item.fee > 0 ? '+' + item.fee : item.fee}}</div>
											<div class="item-title-bottom">剩余 {{item.amount}} 积分</div>
										</div>
									</div>
								</li>
							</ul>
						</div>
						<div class="loaded" v-if="records.finished">
							<div class="loaded-tips">没有更多了</div>
						</div>
					</div>
				</van-list>
				<div class="current-list" v-else>
					<div class="no-data">
						<div class="bg"></div>
						<p>暂无积分明细～</p>
					</div>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import {getRecords} from "@/controller/funcCommon"
	export default {
		components: {
			PublicHeader,
			PublicFooter
		},
		data() {
			return {
				sid: 0,
				records: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
				},
				trade_types: {},
				filter: {
					items: {
						trade_type: ''
					}
				},
				showPreLoading: true,
				isRefresh: false,
			}
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		},
		methods: {
			onLoad(force) {
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'kabao/index/record',
					data: {
						sid: that.sid,
						page: that.records.page,
						psize: that.records.psize
					},
					success: (res) => {
						that.trade_types = Object.assign({}, res.trade_types)
					}
				})
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
			onToggleTradeType: function(value) {
				if(this.filter.items.trade_type == value) {
					return;
				}
				this.filter.items.trade_type = value;
			},
		},
		mounted() {
			var query = this.util.parseQuery(this.$route.query);
			this.sid = query.sid;
			this.onLoad();
		}
	}
</script>

<style>
	#kabao-record .content{
		padding-top: 45px;
	}
	#kabao-record .content .current-list .list-block{
		margin: 8px 0;
		font-size: 17px;
	}
	#kabao-record .content .current-list .list-block .list{
		position: relative;
		padding: 0;
		margin: 0;
		list-style: none;
		background-color: #fff;
	}
	#kabao-record .content .current-list .list-block .list .item-content{
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		min-height: 44px;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		padding-left: 15px;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner{
		position: relative;
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		width: 100%;
		min-height: 44px;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		padding-top: 8px;
		padding-right: 15px;
		padding-bottom: 7px;
		-webkit-box-flex: 1;
		-ms-flex: 1;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-inner-icon{
		position: absolute;
		top: 22px;
		right: 10px;
		font-size: 16px;
		color: #b2b2b2;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-title{
		position: relative;
		max-width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		flex-shrink: 1;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-title .item-title-top{
		font-size: 14px;
		color: #333;
		line-height: 21px;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-title .item-title-bottom{
		font-size: 14px;
		color: #9A999A;
		line-height: 21px;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-after{
		display: inline-block;
		max-width: 300px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-after .item-title-top{
		color: #f6383a;
		font-size: 14px;
		line-height: 21px;
		text-align: right;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-after .item-title-top.color-success{
		color: #4cd964!important;
	}
	#kabao-record .content .current-list .list-block .list .item-content .item-inner .item-after .item-title-bottom{
		color: #9A999A;
		font-size: 14px;
		line-height: 21px;
		text-align: right;
	}
	#kabao-record .content .current-list .no-data{
		padding-top: 30%;
		text-align: center;
	}
	#kabao-record .content .current-list .no-data .bg{
		display: inline-block;
		width: 160px;
		height: 160px;
		background: url(../../../static/img/no_order.png) no-repeat 0 0;
		background-size: cover;
	}
	#kabao-record .content .current-list .no-data p{
		margin: 0;
		color: #999;
		font-size: 18px;
	}
</style>


